@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
:root {
    --accent: rgb(252, 185, 65);
    --dark: rgb(51, 51, 51);
    --white: #f5f5f5;
    --text-h1: 2rem;
    --text-mid: 1.3rem;
    --text-default: 1rem;
    --text-small: .875rem;
    --font-roboto: 'Roboto', 'sans-serif';
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Oswald', sans-serif;
}

.container {
    max-width: 1200px;
}

a {
    color: var(--white);
    text-decoration: none;
}

a:hover {
    color: var(--accent);
}

/* Header */


header {
    background-color: var(--dark);
    color: var(--white);
    position: relative;
}

.logo {
    color: var(--accent);
}

.header-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    color: var(--white);
    font-size: var(--text-mid);
    padding: 1em .2em;
}

.header-cart-items {
    display: flex;
    align-items: baseline;
}

.header-cart {
    position: relative;
    margin-right: .4em;
}

.header-cart-items,
.header-cart-items a {
    color: transparent;
    -webkit-text-stroke: 1px var(--white);
}

.cart-total-display {
    position: absolute;
    right: -10px;
    top: 0;
    -webkit-text-stroke: 0px;
    color: var(--white);
    background: var(--accent);
    border-radius: 50%;
    font-size: .675rem;
    height: 15px;
    width: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--dark);
    font-weight: 700;
}

.header-cart-items i {
    margin-left: .5em;
}

.search-bar {
    position: relative;
    width: 300px;
}

.box {
    color: black;
    width: 500px;
    height: 100px;
    background-color: var(--white);
    position: absolute;
    left: 50%;
    top: 40px;
    z-index: 9999999;
    transform: translateX(-50%);
    overflow: scroll;
    display: none;
    font-size: var(--text-small);
    padding: 1em;
}

.box a {
    color: var(--dark);
}

.search-bar:hover .box {
    display: block;
}

.search-bar input {
    height: 40px;
    width: 100%;
    padding: 1em;
    font-size: var(--text-small);
    border: none;   
    font-family: inherit;
    letter-spacing: .1em;
}

.search-bar input:focus {
    outline: none;  
}

.search-bar i {
    position: absolute;
    top: 50%;
    right: .7em;
    color: var(--accent);
    transform: translateY(-50%);
    cursor: pointer;
}

.navigation {
    display: flex;
    justify-content: center;
}

hr {
    border: none;
    height: 1px;
    background-color: var(--white);
    margin: 0;
}

.navigation li {
    list-style: none;
    padding: .8em 2em;
    text-transform: uppercase;
}

.logo a {
    color: var(--accent);
}

ul {
    margin-bottom: 0;
}

.hamburger-menu {
    display: none;
}

.x-mark {
    display: none;
}

/* Home */

.category {
    margin: 2em 0;
    font-family: var(--font-roboto);
}

.home-category-title {
    font-size: 1.8rem;
    font-weight: 500;
    text-align: center;
    font-family: var(--font-roboto);
    margin-top: 1.5em;
}

.category-title {
    text-transform: capitalize;
    font-size: 1.6rem;
    font-weight: 400;
}

.category-product-img {
    width: 100%;
    display: block;
    object-fit: contain;
    aspect-ratio: 1/1;
}

.category-item {
    font-family: var(--font-roboto);
    padding: 1em;
    min-height: 250px;
    display: flex;
    flex-direction: column;
    border: 1px solid rgba(0,0,0, 0.2);
    border-radius: 4px;
}

.category-item p {
    font-weight: 500;
    height: 45px;
    overflow: hidden;
    font-size: var(--text-small);
}

.category {
    position: relative;
}

.owl-dots {
    display: none;
}

.owl-theme .owl-nav {
    font-size: 1.5rem;
}

.view-all {
    display: inline-block;
    padding: .2em 1em;
    position: absolute;
    top: .4em;
    right: 0;
    text-transform: uppercase;
    font-size: var(--text-small);
    background-color: var(--dark);
    color: var(--white);
}

.view-all:hover {
    color: var(--accent);
}

.category-item p:nth-child(3) {
    font-size: 1rem;
    color: green;
    height: 20px;
    margin-bottom: 0;
}

.owl-carousel .owl-nav button.owl-prev,
.owl-carousel .owl-nav button.owl-next {
    padding: 1em;
}

.product-cart,
.category-item .fa-heart {
    cursor: pointer;
    font-size: 1rem;
    position: absolute;
    opacity: 0;
    top: .5em;
    border-radius: 50%;
    height: 32px;
    width: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(252, 185, 65, .65);
    color: var(--dark);
}

.category-item:hover .product-cart,
.category-item:hover .fa-heart {
    opacity: 1;
}

.fa-heart {
    right: .5em;
}

.category-item i:hover {
    transform: scale(1.2);
}

.category-item:hover > i {
    transition: all 600ms ease;
}

.product-title {
    margin-top: 1em;
    color: black;
}

.product-info {
    display: flex;
    justify-content: space-between;
    max-height: 20px;
}

.product-rating {
    display: flex;
}

.product-info i {
    color: gold;
    margin-right: .2em;
}


.product-info i:hover {
    transform: none;
}

/* Product Detail */

.product-detail {
    font-family: var(--font-roboto);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1em;
}

.product-detail img {
    width: 100%;
    aspect-ratio: 1/1;
    object-fit: contain;
}

.product-detail hr {
    background-color: black;
    margin: .8em 0;
}

.product-detail-btn {
    display: inline-block;
    padding: .2em 1em;
    background-color: var(--accent);
}

.product-detail-btn:hover {
    background-color: var(--dark);
    color: var(--accent);
}

/* Cart */

.cart {
    min-height: 60vh;
    font-family: var(--font-roboto);
}

.cart-item {
    font-family: var(--font-roboto);
    padding: 1em;
    display: grid;
    grid-template-columns: 1fr 1fr 4fr 1fr 1fr;
    grid-auto-rows: 1fr;
    border: 1px solid var(--dark);
    gap: .2em;
}

.delete-btn {
    display: inline-block;
    width: fit-content;
    height: fit-content;
    margin: 0 auto;
    background-color: transparent;
    padding: .2em;
    border: none;
}

.cart-item img {
    display: block;
    height: 100px;
    aspect-ratio: 1/1;
    object-fit: contain;
}

.no-items {
    min-height: 60vh;
}

.total {
    display: flex;
    justify-content: space-between;
}

/* Carousel */

.carousel-item img {
    min-height: 600px;
    object-fit: cover;
    object-position: center;
    position: relative;
    height: 100%;
}

.carousel-text {
    position: absolute;
    top: 50%;
    left: 20%;
    transform: translate(-20%, -50%);
    color: var(--white);
    text-shadow: 2px 3px 0px rgba(0,0,0,0.5);
}

.carousel-text h4 {
    color: var(--accent);
    font-size: 2rem;
}

.carousel-text h2 {
    font-size: 4rem;
}

.carousel-text a {
    background-color: var(--dark);
    padding: .5em 1em;
    display: inline-block;
    border-radius: 3px;
}

.parallax-container {
    position: relative;
    min-height: 400px;
    overflow: hidden;
}

.parallax-image {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.parallax-container::before {
    content: '';
    height: 500px;
    width: 100%;
    background-color: rgb(0,0,0, .5);
    position: absolute;
    z-index: 99;
}

.sign-up {
    position: absolute;
    top: 50%;
    left: 50%;
    color: var(--white);
    z-index: 999;
    transform: translate(-50%, -50%);
    text-transform: uppercase;
}

.sign-up input {
    width: 100%;
    padding: .3em;
}

.sign-up input:focus {
    outline: none;
}

.input-area {
    display: flex;
    gap: .2em;
}

.input-area input {
    outline: none;
    border: 1px solid var(--dark);
    border-radius: 20px;
    padding: 0.3em 1em;
}

.input-area button {
    border: none;
    display: inline-block;
    padding: 0em;
    width: 30%;
    border: 1px solid var(--dark);
    background-color: var(--accent);
    color:  var(--dark);
    border-radius: 20px;
}

/* Products */
.products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1em;
}

ol, ul {
    padding-left: 0;
}

.category-item {
    position: relative;
}

.products > .category-item .product-cart {
    position: absolute;
    top: .5em;
    left: .5em;
}


/* Footer */

footer {
    background-color: var(--dark);
    padding: 3em 0;
    color: var(--white);
}

footer p, footer a {
    margin-bottom: 0;
    font-size: var(--text-small);
    font-weight: 400;
    font-family: var(--font-roboto);
}

.footer-nav {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.footer-nav ul {
    padding-left: 0;
}

.footer-nav ul li {
    list-style: none;
    margin-left: 0;
}

.footer-nav h4 {
    text-transform: uppercase;
    font-weight: 300;
    position: relative;
}

.footer-nav h4::before {
    content: '';
    width: 20%;
    height: 2px;
    background-color: var(--accent);
    position: absolute;
    bottom: -0.2em;
}

/* Brands */

.brands {
    padding: 1em 0;
}

/* Media Queries */

@media (max-width: 768px) {
    .carousel-caption {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .carousel-text {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    nav {
        position: relative;
    }

    .hamburger-menu {
        display: block;
        padding: .5em;
        font-size: 1.2rem;
        cursor: pointer;
    }

    .navigation {
        position: fixed;
        top: 0;
        left: -100%;
        padding: 0;
        flex-direction: column;
        z-index: 1000;
        background-color: rgb(51, 51, 51, .95);
        height: 100vh;
        width: 60%;
        text-align: center;
        opacity: 0;
        transition: all 500ms cubic-bezier(0.645, 0.045, 0.355, 1);
    }

    .navigation-active {
        left: 0;
        top: 0;
        opacity: 1;
    }

    .x-mark {
        display: block;
        position: absolute;
        top: 1em;
        right: 1em;
        font-size: 2rem;
        cursor: pointer;
    }

    .parallax-container {
        min-height: 300px;
    }

    .input-area input,
    .input-area button {
        border-radius: 0;
    }

    .carousel-item h2 {
        font-size: 3rem;
    }

    .carousel-item h4 {
        font-size: 1.8rem;
    }

    .carousel-item img {
        object-position: 70%;
    }

    .footer-nav {
        grid-template-columns: repeat(2, 1fr);
        gap: 1em;
    }

    .product-detail {
        grid-template-columns: 1fr;
        padding: 1em 0;
    }

    .product-detail img {
        max-height: 300px;
    }
}

@media (max-width: 500px) {
    .search-bar {
        max-width: 200px;
        font-size: 1rem;
    }

    .search-bar input {
        height: 20px;
        padding: 1em;
    }

    .products {
        grid-template-columns: repeat(2, 1fr);
    }

    .box {
        width: 300px;
        height: 200px;
        top: 28px;
        border-radius: 5px;
    }
}
